// Parameters
const debug = false;

const serverURL = debug ?
    'http://localhost:8000' :
    ""
const debugURL = serverURL + "/lyrics_svc/lyrics?song=我很好騙&artist=Power Station";

const ACTION_WIDTH = 10;
const INTERVAL_OF_RECOVERING_AUTO_SCROLL_AFTER_USER_SCROLL = 3000;
const FF_MS = 1000;
const RR_MS = 1000;

// Auth component
class SpotifyAuth extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isSpotifyAuth: false,
            userName: "",
        }
    }

    getSpotifyAuthStatus() {
        fetch('/spotify/auth/status', {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
            },
        })
            .then(response => response.json())
            .then(data => {
                this.setState({
                    isSpotifyAuth: data.isSpotifyAuth,
                    userName: data.userName,
                });
            })
            .catch(error => console.log(error));
    }

    componentDidMount() {
        this.getSpotifyAuthStatus();
    }

    render() {
        return (
            <div>
                {this.state.isSpotifyAuth ? "Logged in as " + this.state.userName : "Not logged in"}
                <br></br>
                <form>
                    <button
                        type='submit'
                        formAction={this.state.isSpotifyAuth ? "/spotify/logout" : "/spotify/auth"}>
                        {this.state.isSpotifyAuth ? <LogoutIcon /> : <LoginIcon />}
                    </button>
                </form>
            </div>
        );
    }
}

// Spotify component
class SpotifyStatus extends React.Component {
    render() {
        return (
            <div>
                <button onClick={this.props.onClickSync}><SyncIcon /></button>
                <br></br>
                <p><MusicNoteIcon /> {this.props.song}</p>
                <p><PersonIcon /> {this.props.artist}</p>
                <p><AlbumIcon /> {this.props.album}</p>
                {/* <br></br>
                <button onClick={this.props.onClickSpotifyPrev}><SkipPreviousIcon /></button>
                <button onClick={this.props.onClickSpotifyNext}><SkipNextIcon /></button> */}
            </div>
        )
    }
}

// Timer component
class TimerItem extends React.Component {
    render() {
        return (
            <div className="timer">
                <div className={"text-center"}>
                    <label className={" px-4 py-4 text-center"}>{`${msToTime(this.props.progress_ms)}/${msToTime(this.props.duration_ms)}`}</label>
                    <br></br>
                </div>
                <div>
                    <button
                        onClick={this.props.handleStart}
                        className={`rounded bg-${this.props.running ? 'red' : 'green'} px-4 py-2 mx-2 w-1/3`}>
                        {this.props.running ? <StopIcon /> : <PlayArrowIcon />}
                    </button>
                    <button
                        onClick={this.props.handleClear}
                        className={"rounded bg-grey px-4 py-2 mx-2 w-1/3"}>
                        <RestartAltIcon />
                    </button>
                </div>
                <div>
                    {/* <input type="text" value={this.props.startms} onChange={this.props.updateTimer} /> */}
                    <button onClick={this.props.handleRW}><Replay5Icon /></button>
                    <button onClick={this.props.handleFF}><Forward5Icon /></button>
                </div>
            </div>
        )
    }
}

// Helper functions
const lineRenderer = ({ active, line }) => {
    return (
        <LyricLine active={active} content={line.content} />
    );
};

const msToTime = (duration) => {
    var milliseconds = Math.floor((duration % 1000) / 100),
        seconds = Math.floor((duration / 1000) % 60),
        minutes = Math.floor((duration / (1000 * 60)) % 60);
    minutes = (minutes < 10) ? "0" + minutes : minutes;
    seconds = (seconds < 10) ? "0" + seconds : seconds;

    return minutes + ":" + seconds + "." + milliseconds;
}

const Scrollbar = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
`;
const UiStyle = styled.div`
  width: 100%;
  height: 100%;
  min-width: 720px;
  min-height: 720px;
  display: flex;
  > .operation {
      padding: 10px 10px 10px 10px;
    min-width: 0;
    width: 15%;
    display: flex;
    flex-direction: column;
  }
`;
const LrcStyle = styled.div`
  flex: 1;
  min-width: 0;
  display: flex;
  flex-direction: column;
  padding: 10px ${ACTION_WIDTH}px 10px 0;
  position: relative;
  > .lrc {
    flex: 1;
    min-height: 0;
    height: 100px;
    font-size: 50px;
  }
  > .action {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
    width: ${ACTION_WIDTH}px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
`;

class LyricsFrontend extends React.Component {
    constructor() {
        super();
        this.state = {
            // UI props
            isNight: false,
            isAutoAdvance: true,
            match: null,
            lrc: null,
            error: null,
            // Timer props
            progress_ms: 0,
            duration_ms: 5000,
            running: false,
            time: '',
            startTime: null,
            // Spotify props
            song: '',
            artist: '',
            album: '',
            spotId: '',
            isPlaying: false,
        };
        this.handleStart = this.handleStart.bind(this);
        this.handleClear = this.handleClear.bind(this);
        this.updateTimer = this.updateTimer.bind(this);
        this.handleFF = this.handleFF.bind(this);
        this.handleRW = this.handleRW.bind(this);
    }
    // Timer stuffs
    updateTimer = (event) => {
        this.setState({ progress_ms: event.target.value * 1000 });
    }
    handleStart = () => {
        this.setState({ startTime: Date.now() - this.state.progress_ms });
        if (this.state.running) {
            clearInterval(this.state.time);
            this.setState({ running: false });
        } else {
            this.setState({
                time: setInterval(() => {
                    // update timer
                    this.setState({ progress_ms: Date.now() - this.state.startTime });
                    // if next song, auto advance
                    if (this.state.progress_ms >= this.state.duration_ms) {
                        // Reset timer needs to set running, progress_ms and start_time
                        clearInterval(this.state.time);
                        this.setState({
                            running: false,
                            progress_ms: 0,
                        });
                        this.fetchSpotifyStatus();
                    }
                }, 10)
            });
            this.setState({ running: true });
        }
    }
    handleStartAuto = () => {
        this.setState({ startTime: Date.now() - this.state.progress_ms });
        if (!this.state.running) {
            this.setState({
                time: setInterval(() => {
                    // update timer
                    this.setState({ progress_ms: Date.now() - this.state.startTime });
                    // // if next song, auto advance
                    if (this.state.progress_ms >= this.state.duration_ms) {
                        // Reset timer needs to set running, progress_ms and start_time
                        clearInterval(this.state.time);
                        this.setState({
                            running: false,
                            progress_ms: 0,
                        });
                        this.fetchSpotifyStatus();
                    }
                }, 10)
            });
            this.setState({ running: true });
        }
    }
    handleClear = () => {
        this.setState({
            progress_ms: 0,
            running: false
        });
        clearInterval(this.state.time);
    }
    handleFF = () => {
        this.setState({ startTime: this.state.startTime - FF_MS });
    }
    handleRW = () => {
        this.setState({ startTime: this.state.startTime + RR_MS });
    }

    // Spotify stuffs
    loadSpotify = (resData) => {
        this.setState({
            song: resData.name,
            artist: resData.artist,
            album: resData.album,
            progress_ms: resData.progress_ms,
            duration_ms: resData.duration_ms,
            isPlaying: resData.is_playing,
        });
        this.fetchLrc(resData.name, resData.artist);
    }
    fetchSpotifyStatus = () => {
        console.log("Here we go!");
        let url = serverURL + "/spotify/player";
        // Manual pseudo-synchronous calls to prevent looping in autostart
        setTimeout(() => { this.handleClear(); }, 10);
        setTimeout(() => {
            fetch(url)
                .then(response => response.json())
                .then(this.loadSpotify)
                .catch(error => {
                    this.setState({ error: "Spotify status error: " + error.message });
                });
        }, 100)
        setTimeout(() => { this.handleStartAuto(); }, 1100);
    }
    // handleSpotifyNext = () => {
    //     fetch(serverURL + "/spotify/player/next")
    //         .then(this.fetchSpotifyStatus())
    //         .catch(error => {
    //             this.setState({ error: "Spotify control error: " + error.message });
    //         });
    // }
    // handleSpotifyPrev = () => {
    //     fetch(serverURL + "/spotify/player/previous")
    //         .then(this.fetchSpotifyStatus())
    //         .catch(error => {
    //             this.setState({ error: "Spotify control error: " + error.message });
    //         });
    // }
    // Lyrics stuffs
    buildLrc = (resData) => {
        // Take the response data, pass it into state
        this.setState({ match: resData.match });
        if (resData.match > 0) {
            this.setState({ lrc: resData.data[0].lrc });
        } else {
            this.setState({ lrc: "Nothing!" });
        }
    }

    fetchLrc(song, artist, spotId) {
        // spotId just for future use in case
        let url = serverURL + "/lyrics_svc/lyrics?song=" + song + "&artist=" + artist;
        url = debug ? debugURL : url;

        fetch(url)
            .then(response => response.json())
            .then(this.buildLrc)
            .catch(error => {
                this.setState({ error: "fetchLrc error: " + error.message });
            });
    }

    render() {
        return (
            <Scrollbar>
                <UiStyle>
                    <div className='operation'>
                        <SpotifyAuth />
                        <SpotifyStatus
                            song={this.state.song}
                            artist={this.state.artist}
                            album={this.state.album}
                            onClickSync={() => this.fetchSpotifyStatus()}
                        // onClickSpotifyPrev={() => this.handleSpotifyPrev()}
                        // onClickSpotifyNext={() => this.handleSpotifyNext()}
                        />
                        <TimerItem
                            progress_ms={this.state.progress_ms}
                            duration_ms={this.state.duration_ms}
                            running={this.state.running}
                            handleStart={() => this.handleStart()}
                            handleClear={() => this.handleClear()}
                            handleFF={() => this.handleFF()}
                            handleRW={() => this.handleRW()}
                            updateTimer={() => this.updateTimer()}
                        />
                    </div>
                    <LrcStyle>
                        {this.state.error && <h3>Error: {this.state.error}</h3>}
                        {this.state.lrc &&
                            <Lrc
                                className='lrc'
                                lrc={this.state.lrc.replaceAll('\r', '')}
                                lineRenderer={lineRenderer}
                                currentMillisecond={this.state.progress_ms}
                                autoScroll={true}
                                topBlank={false}
                                bottomBlank={false}
                                intervalOfRecoveringAutoScrollAfterUserScroll={INTERVAL_OF_RECOVERING_AUTO_SCROLL_AFTER_USER_SCROLL}
                            />
                        }
                    </LrcStyle>
                </UiStyle>
            </Scrollbar>
        )
    }
}

ReactDOM.render(
    <LyricsFrontend />,
    document.getElementById('root')
);
