import React from 'react';
import styled, { css } from 'styled-components';

const Style = styled.div`
  /* padding: 10px 0px; */
  font-size: 1em;
  line-height: 2em;
  background-color: white;
  ${({ active }) => css`
    color: ${active ? 'blue' : '#222'};
  `}
`;

const LyricLine = ({ active, content }) => {
  return (
    <Style active={active}>
      {content}
    </Style>
  );
};

export default LyricLine;